*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Merienda", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/static/media/desktop-bg.2e96c35b.jpg) no-repeat,
    linear-gradient(to top, #efefef, #ffffff);
  background-size: 100% auto;
  height: 100vh;
  position: relative;
}

.header {
  text-align: center;
  padding-top: 30px;
}
.header--title:before {
  content: "";
  width: 30px;
  height: 1px;
  background-color: #000;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}

.header--title:after {
  content: "";
  width: 30px;
  height: 1px;
  background-color: #000;
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
}

.header--title {
  font-size: 20px;
  font-style: italic;
}
.header--titlename {
  font-size: 26px;
  font-weight: bold;
  padding-top: 10px;
}
.header--mobile {
  display: none;
}
section.aboutme {
  padding-top: 50px;
  height: 140px;
  text-align: center;
}
section.aboutme span {
  font-size: 32px;
  text-transform: uppercase;
}
section.contactme {
  text-align: center;
  padding-top: 50px;
}

section.contactme > div {
  padding-bottom: 20px;
  font-size: 20px;
}

section.contactme a {
  text-decoration: none;
  color: #000;
}

section.contactme svg {
  margin-left: 10px;
}
footer.footer {
  position: absolute;
  bottom: 15px;
  width: 100%;
  text-align: center;
  height: 90px;
}
.socials ul {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  padding: 0;
  display: flex;
}

.socials ul li {
  list-style: none;
}

.socials ul li a {
  position: relative;
  width: 60px;
  height: 60px;
  display: block;
  text-align: center;
  margin: 0 10px;
  border-radius: 50%;
  padding: 6px;
  box-sizing: border-box;
  text-decoration: none;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
  background: linear-gradient(0deg, #ddd, #fff);
  transition: 0.5s;
}

.socials ul li a:hover {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}

.socials ul li a svg {
  width: 50% !important;
  height: 100%;
  display: block;
  background: linear-gradient(0deg, #fff, #ddd);
  border-radius: 50%;
  line-height: calc(60px - 12px);
  font-size: 24px;
  color: #262626;
  transition: 0.5s;
  margin: 0 auto;
}

.socials ul li:nth-child(1) a:hover svg {
  color: #3b5998;
}

.socials ul li:nth-child(2) a:hover svg {
  color: #00aced;
}

.socials ul li:nth-child(3) a:hover svg {
  color: #dd4b39;
}

.socials ul li:nth-child(4) a:hover svg {
  color: #007bb6;
}
.socials {
  position: relative;
}

.footer > span {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  display: block;
  left: 0;
}
.footer a.a2hs {
  color: #000;
  font-size: 10px;
  line-height: 16px;
  font-weight: bold;
}

/*Loader CSS*/
section#loader {
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  position: absolute;
  width: 100%;
  text-align: center;
}
.loading {
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  padding: 0.5em;
  vertical-align: middle;
  text-align: center;
  background-color: transparent;
  border: 5px solid transparent;
  border-top-color: #c22;
  border-bottom-color: #c22;
  border-radius: 50%;
}

.outer {
  animation: spin 1s infinite;
}

.inner {
  animation: spin 1s infinite;
}
.load-text > span {
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 1px;
}

.load-text {
  padding-top: 20px;
}
@keyframes spin {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
@media only screen and (max-width: 1024px) {
  body {
    overflow: scroll;
    background: none;
    height: auto;
    position: static;
  }
  footer.footer {
    position: relative;
    margin-top: 50px;
  }
  img {
    max-width: 100%;
    height: auto;
  }
  .header--mobile {
    display: block;
  }
  header.header {
    padding-top: 0px;
  }
  section.aboutme {
    height: 160px;
  }
  section.aboutme,
  section.contactme {
    max-width: 95%;
    margin: 0 auto;
  }
  .socials ul {
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
  }
}

